<template lang="html">
  <div class="lsgj-wrapper">
    <div class="content">
          <div id="allmap"></div>
    </div>
    <div class="dingwei">
      <p class="title">每次定位时,司机所在的位置颜色越深,表示在某地的次数越多</p>
      <ul class="menu">
        <li>
          <span class="icon iconfont icon-dingwei one"></span>
          <span>经常(15次以上)</span>
        </li>
        <li>
          <span class="icon iconfont icon-dingwei two"></span>
          <span>经常(10至14次)</span>
        </li>
        <li>
          <span class="icon iconfont icon-dingwei three"></span>
          <span>经常(5至9次)</span>
        </li>
        <li>
          <span class="icon iconfont icon-dingwei four"></span>
          <span>经常(1至4次)</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  mounted(){
    // 百度地图API功能
	var map = new BMap.Map("allmap");
	map.centerAndZoom(new BMap.Point(116.4035,39.915),8);
	setTimeout(function(){
		map.setZoom(14);
	}, 1000000);  //2秒后放大到14级
	map.enableScrollWheelZoom(true);
  }
}
</script>

<style lang="less" scoped>
.lsgj-wrapper{
  width: 1249px;
  margin: 0 auto;
  position: relative;
  .content{
    width: 1249px;
    background: #fff;
    height: 760px;
    #allmap{
      border-top: 2px solid #415089;
      width: 1178px;
      height: 720px;
      margin: 0 30px;
    }
  }
  .dingwei{
    width: 280px;
    height: 224px;
    background: #fff;
    position: absolute;
    right: 60px;
    bottom: 50px;
    .title{
      padding: 26px 20px;
      line-height: 18px;
    }
    .menu{
        padding-left: 20px;
      li{
        padding: 0 0 12px 0;
        color: #999;
        .one{
          color:#0d46b1;
        }
        .two{
          color:#0065fd;
        }
        .three{
          color:#73a2f0;
        }
        .four{
          color:#9bdcfc;
        }
      }
    }
    }
  }
</style>



